import { BaseRepo } from '../repo/base_repo';
import { BaseEntity } from './base_entity';
import {IncomeExpenditureRepo} from '../repo/income_expenditure_repo';

//'/' 
class IncomeExpenditureDetail extends BaseEntity {
    /**
     * @type {IncomeExpenditureType}
     */
    type = null;
    //
    desc = null;
    //
    money = null;
    //
    happenTime = null;

    static repo = new IncomeExpenditureRepo()

    static query(id) {
        return this.repo.get(id)
    }

    static queryTimeBetwen(startTime, endTime) {
        return this.repo.select(startTime, endTime)
    }

    static delete(id) {
        IncomeExpenditureDetail.repo.delete(id)
    }

    save() {
        this.gmtModified = new Date()
        if(this.money*this.type.code < 0) {
            this.money = -1 * this.money
        }
        IncomeExpenditureDetail.repo.upsert(this)
    }
}


class IncomeExpenditureType {
    static Incomme = {
        salary: {
            self: new IncomeExpenditureType(10000, ""),
            num13: new IncomeExpenditureType(10001, "-"),
            bonus: new IncomeExpenditureType(10002, "-"),
        },
        luckmoney: {
            self: new IncomeExpenditureType(20000, ""),
            work: new IncomeExpenditureType(20001, "-"),
            home: new IncomeExpenditureType(20002, "-"),
        },
        other: {
            self: new IncomeExpenditureType(30000, ""),
        }
    };

    static Expenditure = {
        home: {
            self: new IncomeExpenditureType(-10000, ""),
            rent: new IncomeExpenditureType(-10001, "-"),
            utility: new IncomeExpenditureType(-10002, "-"),
        },
        daily: {
            self: new IncomeExpenditureType(-20000, ""),
            taxi: new IncomeExpenditureType(-20001, "-"),
            phone: new IncomeExpenditureType(-20002, "-"),
        },
        shopping: {
            self: new IncomeExpenditureType(-30000, ""),
            elc: new IncomeExpenditureType(-30001, "-"),
            life: new IncomeExpenditureType(-30002, "-"),
        },
        happy: {
            self: new IncomeExpenditureType(-40000, ""),
            online: new IncomeExpenditureType(-40001, "-"),
            offline: new IncomeExpenditureType(-40002, "-"),
        },
        food: {
            self: new IncomeExpenditureType(-50000, ""),
            canteen: new IncomeExpenditureType(-50001, "-"),
            out: new IncomeExpenditureType(-50002, "-"),
        },
        family: {
            self: new IncomeExpenditureType(-60000, ""),
            traval: new IncomeExpenditureType(-60001, "-"),
            gift: new IncomeExpenditureType(-60002, "-"),
        },
        study: {
            self: new IncomeExpenditureType(-70000, ""),
            online: new IncomeExpenditureType(-70001, "-"),
            offline: new IncomeExpenditureType(-70002, "-"),
        },
        health: {
            self: new IncomeExpenditureType(-80000, ""),
        },
        car: {
            self: new IncomeExpenditureType(-90000, ""),
            etc: new IncomeExpenditureType(-90001, "-/ETC"),
            oil: new IncomeExpenditureType(-90002, "-"),
        },
        girlfriend: {
            self: new IncomeExpenditureType(-100000, ""),
            traval: new IncomeExpenditureType(-100001, "-"),
            eat: new IncomeExpenditureType(-100002, "-"),
            gift: new IncomeExpenditureType(-100003, "-"),
        },
        other: {
            self: new IncomeExpenditureType(-110000, ""),
        },
    }

    constructor(code, name) {
        this.code = code
        this.name = name
    }

    static getByCode(code) {
        for(const type of this.values()) {
            if(type.code == code) {
                return type
            }
        }
        return null
    }

    static toList(data) {
        if(data instanceof IncomeExpenditureType) {
            return [data]
        } else {
            let keys = Object.keys(data)
            let result = []
            for(const key of keys) {
                result = result.concat(this.toList(data[key]))
            }
            return result
        }
    }

    static values() {
        return this.toList(this.Incomme).concat(this.toList(this.Expenditure))
    }

    getGroup() {
        return IncomeExpenditureType.getByCode(this.code - this.code % 100)
    }

    isIncome() {
        return this.code > 0
    }
}

export {IncomeExpenditureDetail, IncomeExpenditureType}
